import { Component, Input, OnInit } from '@angular/core';
import { IBreadcrumb } from './models/breadcrumb.interface';
import { BcBreadcrumbBehavior } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'bc-breadcrumb',
  templateUrl: './bc-breadcrumb.component.html',
  styles: []
})
export class BcBreadcrumbComponent implements OnInit {
  bcBreadcrumbBehavior: BcBreadcrumbBehavior;
  breadcrumbs: IBreadcrumb[];

  @Input() componentId = this.bcHelperService.getId(this);

  constructor(private bcHelperService: BcHelperService, private activatedRoute: ActivatedRoute) {
    this.bcBreadcrumbBehavior = new BcBreadcrumbBehavior();
  }

  ngOnInit(): void {
    this.breadcrumbs = this.createBreadcrumbs(this.activatedRoute.root);
  }

  ngAfterViewInit() {
    this.bcBreadcrumbBehavior.setId(this.componentId);
    this.bcBreadcrumbBehavior.setUp();
    this.bcBreadcrumbBehavior.changeBreadCrumb();
  }

  private createBreadcrumbs(route: ActivatedRoute, url: string = '', breadcrumbs: IBreadcrumb[] = []): any[] {
    const children: ActivatedRoute[] = route.children;

    if (children.length === 0) {
      return breadcrumbs;
    }

    for (const child of children) {
      const routeURL: string = child.snapshot.url.map((segment) => segment.path).join('/');
      if (routeURL !== '') {
        url += `/${routeURL}`;
      }

      const breadcrumb = child.snapshot.data.breadcrumb;
      if (breadcrumb && !breadcrumbs.map((item) => item.text).includes(breadcrumb)) {
        breadcrumbs.push({ path: url, text: breadcrumb });
      }

      return this.createBreadcrumbs(child, url, breadcrumbs);
    }

    return breadcrumbs;
  }
}
